
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
  font-size: 3rem;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.TitleDiv {
  margin-top: 2rem;
  padding: 2rem;

}
.Title, .navTitle{
  font-family: 'Prata';
}

.figure{
  padding: 3rem;
}

.card-columns{
  margin-top: 3rem;
  margin-bottom: 5rem;

}

p, .navLink, .App-link{
  font-family: 'Poiret One';
  font-size: 23px;
}

.profile{
  padding-bottom: 2rem;
}

.profile, .Projects{
  background-color: #ffb552;
  padding: 2rem;
}

.navBar{
  background-color: #ffb552;
}

.caption{
  margin-top: 3rem;
}

.footer{
  font-size: 15px;
}
